import React from 'react';
import { Button, Modal } from 'react-bootstrap';

export default class TermsAndConditions extends React.Component {
  constructor () {
    super();

    this.toggleAgreed = this.toggleAgreed.bind(this);

    this.state = {
      agreed: false
    };
  }

  toggleAgreed (event) {
    this.setState({ agreed: event.target.checked });
  }

  render () {
    const {
      HideTermsAndConditionsHandler,
      ConfirmTermsAndConditionsHandler,
      ConfirmedTermsAndConditions,
      TermsAndConditions
    } = this.props;

    const showModal = (ConfirmedTermsAndConditions !== true);

    var honninUrl = '';
    var oshiireKojinJouhouUrl = '';
    var oshiireYakkanUrl = '';
    var kojinJouhouUrl = '';
    var hoshouYakkanUrl = '';

    if (TermsAndConditions !== undefined) {
      honninUrl = TermsAndConditions.honnin === undefined ? '' : TermsAndConditions.honnin;
      oshiireKojinJouhouUrl = TermsAndConditions.oshiire_kojin_jouhou === undefined ? '' : TermsAndConditions.oshiire_kojin_jouhou;
      oshiireYakkanUrl = TermsAndConditions.oshiire_yakkan === undefined ? '' : TermsAndConditions.oshiire_yakkan;
      kojinJouhouUrl = TermsAndConditions.kojin_jouhou === undefined ? '' : TermsAndConditions.kojin_jouhou;
      hoshouYakkanUrl = TermsAndConditions.hoshou_yakkan === undefined ? '' : TermsAndConditions.hoshou_yakkan;
    }

    return (
      <Modal show={showModal} onHide={() => {}}>
        <Modal.Header>
          <Modal.Title><strong></strong></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            <strong>  </strong>
            <br />
            <br />
            <strong>JPEGPDF</strong>
            <br />
            <br />
            <a href={honninUrl} target='_blank' rel='noopener noreferrer'></a>
            <br />
            <br />
            <strong></strong>
          </p>

          <ul className='list-unstyled'>
            <li><a href={oshiireKojinJouhouUrl} target='_blank' rel='noopener noreferrer'></a></li>
            <li><a href={oshiireYakkanUrl} target='_blank' rel='noopener noreferrer'></a></li>
            <li><a href={kojinJouhouUrl} target='_blank' rel='noopener noreferrer'></a></li>
            <li><a href={hoshouYakkanUrl} target='_blank' rel='noopener noreferrer'></a></li>
          </ul>

          <div className='checkbox'>
            <label>
              <input type='checkbox' id='check' defaultChecked={false} onChange={this.toggleAgreed.bind(this)} /> 
            </label>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={HideTermsAndConditionsHandler}></Button>
          <Button bsStyle='primary' disabled={!this.state.agreed} onClick={ConfirmTermsAndConditionsHandler}></Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
